.type-block {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  width: 360px;
}

.catalog-type {
  width: calc(33.33% - 10px);
  border: 1px solid rgba(16, 24, 40, 0.2);
  border-radius: 10px;
  padding: 17px 40px;
  overflow: hidden;
}

.catalog-type:hover {
  border: 1px solid #e44848;
}
